@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&family=Unica+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
    display: none;
}

.page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 10px;
  text-align: center;
  width: 100%;
}

.first-page {
  background-color: #5aa7ff;
  text-align: center;
}

.font-weight-300 {
  font-weight: 300;
}

.buttons {
  display: flex;
}
.buttons .btn-projects {
  background-color: #eec607;
}
.buttons .btn-contact {
  background-color: #67c6f5;
}

.btn-custom {
  border-radius: 10px;
  width: 150px;
  height: 50px;
  border: 4px double #5aa7ff;
  transition: 0.3s;
}

.btn-custom:hover {
  color: #353637;
}

.btn-custom:active {
  border: 4px groove rgba(64, 122, 138, 0.4);
}

.btn-blue {
  border: 4px double #278cff;
  background-color: #5aa7ff;
}

.btn-yellow {
  border: 4px double #bc9d06;
  background-color: #e9c207;
}

.project-info {
  display: inline-block;
  max-width: 500px;
}

.project-screenshot {
  max-width: 500px;
  width: 100%;
  border-radius: 30px;
  margin: auto;
}
@media screen and (max-width: 550px) {
  .project-screenshot {
    max-width: 350px;
  }
}

.weather-app {
  background-color: #eec607;
}
.weather-app img {
  border: 8px double #dfba07;
}

.dictionary-app {
  background-color: #5aa7ff;
}
.dictionary-app img {
  border: 8px double #4b9fff;
}

.project {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.mobile-dictionary {
  background-color: #eec607;
}
.mobile-dictionary .dictionary-video {
  margin: auto;
  max-width: 300px;
}
@media screen and (max-width: 750px) {
  .mobile-dictionary .dictionary-video {
    max-width: 200px;
  }
}

@media screen and (max-width: 750px) {
  h3 {
    font-size: 20px;
  }
  .project {
    grid-gap: 10px;
    gap: 10px;
    flex-direction: column;
    justify-content: center;
  }
}/*# sourceMappingURL=home.css.map */
.social-networks a {
  font-size: 24px;
  color: #fff703;
  opacity: 80%;
}

.social-networks a:hover {
  color: yellow;
  opacity: 100%;
}

footer {
  background-color: #479dff;
}/*# sourceMappingURL=footer.css.map */
header {
  position: fixed;
  width: 100%;
  background-color: #479dff;
  z-index: 100;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
}
header a {
  text-decoration: none;
  transition: 0.3s;
}
header .header-wrap {
  height: 60px;
  display: flex;
  justify-content: center;
}
header .header-wrap .full-header {
  position: relative;
  display: flex;
  max-width: 1300px;
  width: 100%;
  padding: 0 10px;
  line-height: 60px;
}
header .header-wrap .full-header .full-nav {
  margin: 0 10px;
}
header .header-wrap .full-header .full-nav a {
  color: rgb(54, 54, 54);
  margin: 0 10px;
}
@media screen and (max-width: 600px) {
  header .header-wrap .full-header {
    justify-content: space-between;
  }
}
header .header-wrap .full-header a:hover {
  color: rgb(0, 0, 0);
}
@media screen and (max-width: 600px) {
  header .header-wrap {
    display: none;
  }
}

.small-header {
  display: none;
}
@media screen and (max-width: 600px) {
  .small-header {
    display: block;
  }
}

.burger-wrap {
  width: 100%;
  padding: 0 10px;
  line-height: 60px;
  background-color: #479dff;
}
.burger-wrap .burger-icon {
  margin-top: 12px;
  line-height: normal;
  float: right;
}
.burger-wrap .burger-icon button {
  background: none;
  border: solid rgb(42, 52, 83) 0.1px;
  border-radius: 5px;
  transition: 0.2s;
}
.burger-wrap button:active {
  background-color: #3d98ff;
}

.dropdown {
  -webkit-animation: dropdown 0.7s ease-in-out forwards;
          animation: dropdown 0.7s ease-in-out forwards;
  -webkit-transform-origin: top center;
          transform-origin: top center;
}
.dropdown ul {
  margin: 0;
  padding: 0;
}
.dropdown ul li {
  line-height: 50px;
  margin: 0 2px;
  padding: 0 10px;
  list-style: none;
  transition: 0.2s;
}
.dropdown ul li a {
  color: rgb(54, 54, 54);
}
.dropdown ul li:hover {
  background-color: aliceblue;
  border-radius: 3px;
}

.logo {
  font-family: "Unica One", cursive;
  font-size: 24px;
  color: black;
}

.logo:hover {
  color: black;
}

@-webkit-keyframes dropdown {
  0% {
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg);
  }
  70% {
    -webkit-transform: rotateX(20deg);
            transform: rotateX(20deg);
  }
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

@keyframes dropdown {
  0% {
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg);
  }
  70% {
    -webkit-transform: rotateX(20deg);
            transform: rotateX(20deg);
  }
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}/*# sourceMappingURL=header.css.map */
